<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-row>
        <el-col :span='8'>
          <el-input
            placeholder=""
          >
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="orderlist" border stripe>
          <el-table-column type='index'></el-table-column>
          <el-table-column label="" prop='order_number'></el-table-column>
          <el-table-column label="" prop='order_price'></el-table-column>
          <el-table-column label="" prop='pay_status'>
              <template slot-scope="scope">
                  <el-tag type='success' v-if="scope.row.pay_status === 1"></el-tag>
                  <el-tag type='danger' v-else></el-tag>

              </template>
          </el-table-column>
          <el-table-column label="" prop='is_send'></el-table-column>
          <el-table-column label="" prop='create_time'>
              <template slot-scope="scope">
                  {{scope.row.create_time | dateFormat }}
              </template>
          </el-table-column>
          <el-table-column label="">
              <template>
              <el-button size="small" type="primary" icon="el-icon-edit" @click="showBox"></el-button>
              <el-button size="small" type="success" icon="el-icon-location" @click="showProgressBox"></el-button>
              </template>
          </el-table-column>

      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="100"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
  title=""
  :visible.sync="addressDialogVisible"
  width="30%"
  @close='addressDialogClosed'>
  <el-form :model="addressForm" :rules="addressFormRules" ref="addressFormRef" label-width="100px">
  <el-form-item label="" prop="address1">
      <el-cascader  :options="options" v-model="addressForm.address1" @change="chooseArea"></el-cascader>
  </el-form-item>
  <el-form-item label="" prop="address2">
    <el-input v-model="addressForm.address2"></el-input>
  </el-form-item>
  </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="addressDialogVisible = false"> </el-button>
    <el-button type="primary" @click="addressDialogVisible = false"> </el-button>
  </span>
</el-dialog>

<!--  -->
<el-dialog
  title=""
  :visible.sync="ProgressDialogVisible"
  width="30%">
  <span></span>
  <span slot="footer" class="dialog-footer">
    <el-button @click="ProgressDialogVisible = false"> </el-button>
    <el-button type="primary" @click="ProgressDialogVisible = false"> </el-button>
  </span>
</el-dialog>
  </div>
</template>
<script>
// 
import { regionData } from 'element-china-area-data'
export default {
  data () {
    return {
      queryInfo: {
        query: '',
        // 
        pagenum: 1,
        // 
        pagesize: 10
      },
      //
      orderlist: [],
      //
      total: '',
      //   
      addressDialogVisible: false,
      ProgressDialogVisible: false,
      options: regionData,
      addressForm: {
        address1: [],
        address2: ''
      },
      addressFormRules: {
        address1: [{ required: true, message: '', trigger: 'blur' }],
        address2: [{ required: true, message: '', trigger: 'blur' }]
      }

    }
  },
  created () {
    this.getOrderList()
  },
  methods: {
    async getOrderList () {
      const { data: res } = await this.$http.get('orders', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      this.total = res.data.total
      this.orderlist = res.data.goods
      console.log(res)
    },
    //  pagesize 
    handleSizeChange (newsize) {
      console.log(newsize)
      this.queryInfo.pagesize = newsize
      this.getOrderList()
    },

    //   
    handleCurrentChange (newpage) {
      console.log(newpage)
      this.queryInfo.pagenum = newpage
      this.getOrderList()
    },
    // 
    showBox () {
      this.addressDialogVisible = true
    },
    //
    addressDialogClosed () {
      this.$refs.addressFormRef.resetFields()
    }
    // async showProgressBox () {
    //   const { data: res } = await this.$http.get('/kuaidi/1106975712662')
    //   console.log(res)
    //   if (res.meta.status !== 200) {
    //     return this.$message.error('')
    //   }
    //   this.ProgressDialogVisible = true
    // }
  }
}
</script>
<style lang="less" scoped>
</style>
