<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card class="box-card">
      <!--  -->
      <el-row>
        <el-col>
          <el-button type="primary"></el-button>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="rolesList" border stripe>
        <!--  -->
        <el-table-column type="expand">
          <template slot-scope='scope'>
            {{scope.row}}
            <el-row v-for="(item1, i1) in scope.row.children" :key='item1.id' :class="['bdbottom', i1 === 0 ? 'bdtop' : '', 'vcenter']">
              <!--   -->
              <el-col :span="6"  >
                <el-tag closable @close='removeRightById(scope.row, item1.id)'>{{item1.authName}}</el-tag>
              </el-col>
              <!--   -->
              <el-col :span="18">
                <el-row v-for="(item2, i2) in item1.children" :key="item2.id" :class="['bdbottom', i2 === 0 ? '' : 'bdtop']">
                  <el-col :span='6'><el-tag closable type='success' @close='removeRightById(scope.row, item2.id)'>{{item2.authName}}</el-tag></el-col>
                  <el-col :span='18'><el-tag closable @close='removeRightById(scope.row, item3.id)' type='warning' v-for="(item3) in item2.children" :key="item3.id">{{item3.authName}}</el-tag></el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="roleName"></el-table-column>
        <el-table-column label="" prop="roleDesc"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button size="mini" type="primary" icon="el-icon-edit"
              ></el-button
            >
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              @click="deletRolesById(scope.row.id)"
              ></el-button
            >
            <el-button size="mini" type="warning" icon="el-icon-setting" @click="showSetRightDialog(scope.row)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </el-card>

   <!--  -->
  <el-dialog
  title=""
  :visible.sync="showRolesDialogVisible"
  width="30%"
  :before-close="handleClose"
  @close='setRightDialogClosed'>
  <!--  -->
  <el-tree :data="rightsList" :props="treeProps" @node-click="handleNodeClick" show-checkbox default-expand-all
  node-key='id' :default-checked-keys="defKeys" ref="treeRef"></el-tree>

  <span slot="footer" >
    <el-button @click="showRolesDialogVisible = false"> </el-button>
    <el-button type="primary" @click="allotRights" > </el-button>
  </span>
</el-dialog>
  </div>
</template>
<script>
export default {
  data () {
    return {
      rolesList: [],
      showRolesDialogVisible: false,
      rightsList: [],
      treeProps: {
        label: 'authName',
        children: 'children'
      },
      defKeys: [],
      roleId: ''
    }
  },
  created () {
    this.getRolesList()
  },
  methods: {
    async getRolesList () {
      const { data: res } = await this.$http.get('roles')
      // console.log(this.$http.get('roles'))
      // console.log(await this.$http.get('roles'))
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.rolesList = res.data
      // console.log(this.rolesList[0].id)
    },
    //  
    async deletRolesById (id) {
      console.log(id)
      console.log(this.rolesList.id)
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
          center: true
        }
      ).catch((err) => err)

      //  confirm
      //  cancel
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }

      const { data: res } = await this.$http.delete('roles/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      this.$message.success('')
      this.getRolesList()
    },
    // id
    async removeRightById (role, rightId) {
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
        center: true
      }).catch(err => err)

      //  confirm
      //  cancel
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      const { data: res } = await this.$http.delete(`roles/${role.id}/rights/${rightId}`)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      role.children = res.data
    },
    // 
    async showSetRightDialog (role) {
      this.roleId = role.id
      const { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status !== 200) {
        this.$message.error('')
      }
      this.rightsList = res.data
      this.getLeafKeys(role, this.defKeys)
      this.showRolesDialogVisible = true
    },
    // id  defKeys
    getLeafKeys (node, arr) {
      if (!node.children) {
        return arr.push(node.id)
      }
      node.children.forEach(item => {
        this.getLeafKeys(item, arr)
      })
    },
    //  
    setRightDialogClosed () {
      this.defKeys = []
    },
    // 
    async  allotRights () {
      const keys = [...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()]

      const idStr = keys.join(',')
      const { data: res } = await this.$http.post(`roles/${this.roleId}/rights`, { rids: idStr })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      this.$message.success('')
      this.getRolesList()
      this.showRolesDialogVisible = false
    }
  }
}
</script>
<style scoped>
.bdtop {
  border-top: 1px solid #eee;
}
.bdbottom {
  border-bottom: 1px solid #eee;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>
