<template>
    <div>

         <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card class="box-card">
       <el-row>
           <el-col>
               <el-button @click="getAddDialogVisible"></el-button>
           </el-col>
       </el-row>

       <!--  -->
       <tree-table class="tree-table" :data='catelist' :columns='columns' :selection-type='false'
       :expand-type='false' show-index index-text='#' border :show-row-hover='false'>

       <template slot="isOk" slot-scope='scope'>
           <i class="el-icon-circle-close" v-if="scope.row.cat_deleted === true" style="color: red"></i>
           <i class="el-icon-circle-check" v-else style="color: lightgreen"></i>
       </template>
       <!--  -->
       <template slot="order" slot-scope='scope'>
           <el-tag v-if="scope.row.cat_level === 0" ></el-tag>
           <el-tag v-else-if="scope.row.cat_level === 1" type='success'></el-tag>
           <el-tag v-else type="warning"></el-tag>
       </template>
       <!--  -->
       <template slot="opt" slot-scope='scope'>
            <el-button size="mini" type="primary" icon="el-icon-edit" @click="moveTreeTable(scope.row.cat_id)"></el-button>
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              @click="deletRolesById(scope.row.cat_id)"
              ></el-button
            >
       </template>
       </tree-table>

       <!--  -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page.sync="querInfo.pagenum"
      :page-size="querInfo.pagesize"
      :page-sizes="[3, 6, 10, 15]"
      layout="total, prev, pager, next,sizes"
      :total="total">
    </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
  title=""
  :visible.sync="addDialogVisible"
  width="30%">
  <el-form :model="addCateForm" :rules="addCateRules" ref="addCateRuleRef" label-width="100px">
  <el-form-item label="" prop="cat_name">
    <el-input v-model="addCateForm.cat_name"></el-input>
  </el-form-item>
  <el-form-item label="">
    <el-cascader
    v-model="cascaderValue"
    :options="parentCateList"
    :props="cascaderProps"
    @change="cascaderChange" class="cascader"></el-cascader>
  </el-form-item>
  </el-form>
  <span slot="footer">
    <el-button @click="addDialogClosed"> </el-button>
    <el-button type="primary" @click="addCate"> </el-button>
  </span>
</el-dialog>
<!-- <div v-for=" item in parentCateList" :key="item.id">{{item}}</div> -->
<!--  -->
<el-dialog
  title=""
  :visible.sync="moveDialogVisible"
  width="30%">
  <el-form ref="moveCateRef" :model="moveCateform" label-width="80px">
  <el-form-item label="">
    <el-input v-model="moveCateform.cat_name"></el-input>
  </el-form-item>
  </el-form>

  <span slot="footer">
    <el-button @click="moveDialogClosed"> </el-button>
    <el-button type="primary" @click="moveCate(scope.row.cat_id)"> </el-button>

  </span>
  </el-dialog>
    </div>
</template>
<script>
export default {
  data () {
    return {
      querInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5
      },
      total: 0,
      //   
      catelist: [],
      //   table
      columns: [
        {
          label: '',
          prop: 'cat_name'
        },
        {
          label: '',
          prop: 'cat_deleted',
          //   
          type: 'template',
          template: 'isOk'
        },
        {
          label: '',
          prop: 'cat_level',
          //   
          type: 'template',
          template: 'order'
        },
        {
          label: '',
          //   prop: 'cat_deleted',
          //   
          type: 'template',
          template: 'opt'
        }
      ],
      //   
      addDialogVisible: false,
      //   
      addCateForm: {
        // 
        cat_name: '',
        //  ID
        cat_pid: 0,
        //   
        cat_level: 0
      },
      // 
      addCateRules: {},
      // 
      parentCateList: [],
      // 
      cascaderProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      cascaderValue: [],
      //  
      moveDialogVisible: false,
      //  
      moveCateform: {
        cat_name: ''
      }
    }
  },
  created () {
    this.getCatelist()
  },
  methods: {
    //   
    async getCatelist () {
      const { data: res } = await this.$http.get('categories', { params: this.querInfo })
      // console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.catelist = res.data.result
      this.total = res.data.total
      // console.log(this.catelist)
    },
    //
    handleSizeChange (newSize) {
      this.querInfo.pagesize = newSize
      this.getCatelist()
    },
    handleCurrentChange (newPage) {
      this.querInfo.pagenum = newPage
      this.getCatelist()
    },
    // 
    getAddDialogVisible () {
      this.addDialogVisible = true
      this.getParentCateList()
    },
    // 
    async getParentCateList () {
      const { data: res } = await this.$http.get('categories', { params: { type: 2 } })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      // console.log(res.data)
      this.parentCateList = res.data

      // console.log(this.parentCateList, '44444444444444444444')
    },
    // 
    cascaderChange () {
      // console.log(this.cascaderValue)
      if (this.cascaderValue.length > 0) {
        // ID
        this.addCateForm.cat_pid = this.cascaderValue[this.cascaderValue.length - 1]
        // 
        this.addCateForm.cat_level = this.cascaderValue.length
      } else {
        // ID
        this.addCateForm.cat_pid = 0
        // 
        this.addCateForm.cat_level = 0
      }
    },
    //   
    addCate () {
      this.cascaderChange()
      this.$refs.addCateRuleRef.validate(async valid => {
        if (!valid) {
          return
        }
        const { data: res } = await this.$http.post('categories', this.addCateForm)
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.getParentCateList()
      })
      this.addDialogVisible = false
      // console.log(this.addCateForm)
    },
    //  ,
    addDialogClosed () {
      this.$refs.addCateRuleRef.resetFields()
      this.cascaderValue = []
      this.addCateForm.cat_pid = 0
      this.addCateForm.cat_level = 0
      this.addDialogVisible = false
    },
    //   
    moveTreeTable (id) {
      // console.log(id)
      this.moveDialogVisible = true
      this.moveCate(id)
    },
    //  
    async moveCate (id) {
      const data = await this.$http.put(`categories/${id}`, { params: this.moveCateform })
      console.log(data)
    },
    //  
    async deletRolesById (id) {
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
        center: true
      }).catch(err => err)
      //  confirm
      //  cancel
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }

      const { data: res } = await this.$http.delete('categories/' + id)
      // console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      this.$message.success('')
      this.getParentCateList()
    }
  }

}
</script>
<style  scoped>
.tree-table {
    margin-top: 15px;
}

</style>
