<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card class="box-card">
      <!--  -->
      <el-alert
        title=""
        type="error"
        show-icon
        close-text=""
      ></el-alert>

      <!--  -->
      <el-row class="elrow">
        <el-col>
          <span>  </span>
          <el-cascader
            v-model="selectedCateKeys"
            :options="cateList"
            :props="cateListProps"
            @change="handleChange"
            clearable
            change-on-select
          ></el-cascader>
        </el-col>
      </el-row>

      <!-- tabs  -->
      <el-tabs v-model="activeName" @tab-click="handleTabClick">
        <!--  -->
        <el-tab-pane label="" name="many">
          <!--  -->
          <el-button type="primary" size="mini" @click="addDialogVisible = true"
            ></el-button
          >
          <!--  -->
          <el-table :data="manyTabList" border stripe>
            <!--  -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <!-- Tag -->
                <el-tag
                  v-for="(item, i) in scope.row.attr_vals"
                  :key="i"
                  closable
                  @close='handleClose(i, scope.row)'
                  >{{ item }}</el-tag
                >
                <!--  -->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                >
                </el-input>
                <!--  -->
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput(scope.row)"
                  >+ New Tag</el-button
                >
              </template>
            </el-table-column>
            <!--  -->
            <el-table-column type="index"></el-table-column>
            <el-table-column
              label=""
              prop="attr_name"
            ></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  size="mini"
                  @click="showEditDialog(scope.row.attr_id)"
                  ></el-button
                >
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="removeParams(scope.row.attr_id)"
                  ></el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <!--  -->
        <el-tab-pane label="" name="only">
          <!--  -->
          <el-button type="primary" size="mini" @click="addDialogVisible = true"
            ></el-button
          >
          <!--  -->
          <el-table :data="onlyTabList">
             <!--  -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <!-- Tag -->
                <el-tag
                  v-for="(item, i) in scope.row.attr_vals"
                  :key="i"
                  closable
                  @close='handleClose(i, scope.row)'
                  >{{ item }}</el-tag
                >
                <!--  -->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                >
                </el-input>
                <!--  -->
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput(scope.row)"
                  >+ New Tag</el-button
                >
              </template>
            </el-table-column>
            <el-table-column
              label=""
              prop="attr_name"
            ></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  size="mini"
                  @click="showEditDialog(scope.row.attr_id)"
                  ></el-button
                >
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="removeParams(scope.row.attr_id)"
                  ></el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>

    <!--  -->
    <el-dialog
      :title="'' + title"
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addDialogClosed"
    >
      <!--  -->
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRef"
        label-width="100px"
      >
        <el-form-item :label="title" prop="attr_name">
          <el-input v-model="addForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addParmas"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="30%"
      @close="editDialogclose"
    >
      <!--  -->
      <el-form
        :model="editForm"
        :rules="editFormRules"
        ref="editFormRef"
        label-width="100px"
      >
        <el-form-item :label="title" prop="attr_name">
          <el-input v-model="editForm.attr_name"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editDialog"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data () {
    return {
      // 
      cateList: [],
      // 
      selectedCateKeys: [],
      // 
      cateListProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // 
      activeName: 'many',
      // 
      manyTabList: [],
      // 
      onlyTabList: [],
      //   
      addDialogVisible: false,
      // 
      addForm: {
        attr_name: ''
      },
      // 
      addFormRules: {
        attr_name: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ]
      },
      //  
      editDialogVisible: false,
      // 
      editForm: {
        attr_name: ''
      },
      // 
      editFormRules: {
        attr_name: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ]
      }
      // 
      // inputVisible: false,
      // 
      // inputValue: ''
    }
  },
  created () {
    this.getCateList()
  },
  computed: {
    // true false
    isBtnDisable () {
      if (this.selectedCateKeys.length !== 3) {
        return true
      }
      return false
    },
    // id
    cateId () {
      if (this.selectedCateKeys.length === 3) {
        return this.selectedCateKeys[2]
      }
      return null
    },
    //  
    title () {
      if (this.activeName === 'many') {
        return ''
      }
      return ''
    }
  },
  methods: {
    // 
    async getCateList () {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.cateList = res.data
      console.log(this.cateList)
    },
    //  
    handleChange () {
      this.getParamsData()
    },
    // tab 
    handleTabClick () {
      console.log(this.activeName)
      this.getParamsData()
    },
    //   
    async getParamsData () {
      // 3
      if (this.selectedCateKeys.length !== 3) {
        this.selectedCateKeys = []
        this.manyTabList = []
        this.onlyTabList = []
        return
      }
      // 3
      console.log(this.selectedCateKeys)
      const { data: res } = await this.$http.get(
        `categories/${this.cateId}/attributes`,
        {
          params: {
            sel: this.activeName
          }
        }
      )
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      // console.log(res.data[0].attr_sel);
      res.data.forEach((item) => {
        item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
        item.inputVisible = false
        item.inputValue = ''
      })

      if (this.activeName === 'only') {
        this.onlyTabList = res.data
      } else {
        this.manyTabList = res.data
      }
      console.log(res)
    },
    // 
    addDialogClosed () {
      this.$refs.addFormRef.resetFields()
    },
    //  
    addParmas () {
      this.$refs.addFormRef.validate(async (valid) => {
        if (!valid) return
        const { data: res } = await this.$http.post(
          `categories/${this.cateId}/attributes`,
          {
            attr_name: this.addForm.attr_name,
            attr_sel: this.activeName
          }
        )
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.addDialogVisible = false
        this.getParamsData()
      })
    },
    //  
    async showEditDialog (id) {
      console.log(id)
      this.editDialogVisible = true
      const { data: res } = await this.$http.get(
        `categories/${this.cateId}/attributes/${id}`,
        {
          params: {
            attr_sel: this.activeName
          }
        }
      )
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.editForm = res.data
      // console.log(this.editForm.attr_id)
      // this.editDialog(id)
      this.$message.success('')
    },
    // 
    editDialogclose () {
      this.$refs.editFormRules.resetFields()
    },
    //   
    editDialog () {
      this.$refs.editFormRef.validate(async (valid) => {
        if (!valid) {
          return
        }
        console.log(this.editForm.attr_id)
        const { data: res } = await this.$http.put(
          `categories/${this.cateId}/attributes/${this.editForm.attr_id}`,
          { attr_name: this.editForm.attr_name, attr_sel: this.activeName }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        this.$message.success('')
      })
      this.getParamsData()
      this.editDialogVisible = false
    },
    // idid
    async removeParams (id) {
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch((err) => err)
      //  confirm
      //  cancel
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }

      const { data: res } = await this.$http.delete(
        `categories/${this.cateId}/attributes/${id}`
      )
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      this.$message.success('')
      this.getParamsData()
    },

    //   Enter 
    async handleInputConfirm (row) {
      if (row.inputValue.trim().length === 0) {
        row.inputValue = ''
        row.inputVisible = false
      }
      // return 
      row.attr_vals.push(row.inputValue.trim())
      row.inputValue = ''
      row.inputVisible = false
      //  
      this.saveAttrVals(row)
    },

    //  
    showInput (row) {
      row.inputVisible = true
      // 
      // $nextTick   
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    //  tag 
    handleClose (i, row) {
      row.attr_vals.splice(i, 1)
      this.saveAttrVals(row)
    },
    // attr-vals
    async saveAttrVals (row) {
      const { data: res } = await this.$http.put(`categories/${this.cateId}/attributes/${row.attr_id}`, {
        attr_name: row.attr_name,
        attr_sel: row.attr_sel,
        attr_vals: row.attr_vals.join(' ')
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
    }
  }
}
</script>
<style  scoped>
.elrow {
  margin-top: 15px;
  margin-left: 3px;
  margin-bottom: 20px;
}

.el-tag {
  margin: 15px;
}
.input-new-tag {
  width: 150px;
}
</style>
