<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!-- Alert -->
      <el-alert title="" type="info" center show-icon> </el-alert>

      <!--  -->
      <el-steps
        :space="200"
        :active="activeIndex - 0"
        finish-status="success"
        align-center
      >
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>

      <!-- Form -->
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRef"
        label-position="top"
      >
        <!-- tab -->
        <el-tabs
          :tab-position="'left'"
          v-model="activeIndex"
          :before-leave="beforeTabLeave"
          @tab-click="tabClick"
        >
          <el-tab-pane label="" name="0">
            <el-form-item label="" prop="goods_name">
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price">
              <el-input v-model="addForm.goods_price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight">
              <el-input v-model="addForm.goods_weight"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number">
              <el-input v-model="addForm.goods_number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_cat">
              <el-cascader
                v-model="addForm.goods_cat"
                :options="CateList"
                :props="cateProps"
                @change="handleChange"
              ></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="1">
            <el-form-item
              :label="item.attr_name"
              v-for="item in manyTabList"
              :key="item.attr_id"
            >
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox
                  :label="cb"
                  v-for="(cb, i) in item.attr_vals"
                  :key="i"
                  border
                ></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <el-form-item
              :label="item.attr_name"
              v-for="item in onlyTabList"
              :key="item.attr_id"
            >
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="3"
            ><el-upload
              :action="uploadURL"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              list-type="picture"
              :headers='headersObj'
              :on-success='handleSuccess'
            >
              <el-button size="small" type="primary"></el-button>
              <div slot="tip" class="el-upload__tip">
                jpg/png500kb
              </div>
            </el-upload></el-tab-pane
          >
          <el-tab-pane label="" name="4">
            <quill-editor v-model="addForm.goods_introduce"></quill-editor>
            <el-button type='primary' class="btn" @click="add"></el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>

<!--  -->
<el-dialog
  title=""
  :visible.sync="PreviewDialogVisible"
  width="50%">
  <img :src="previewPath" alt="">
</el-dialog>

  </div>
</template>
<script>
import _ from 'lodash'
export default {
  data () {
    return {
      activeIndex: '0',
      // 
      addForm: {
        goods_name: '',
        goods_price: 0,
        goods_number: 0,
        goods_weight: 0,
        // 
        goods_cat: [],
        // 
        pics: [],
        // 
        goods_introduce: '',
        //
        attrs: []
      },
      //   
      addFormRules: {
        goods_name: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        goods_price: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        goods_number: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        goods_weight: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ]
      },
      //   
      CateList: [],
      // 
      cateProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      //   
      manyTabList: [],
      //   
      onlyTabList: [],
      // URL
      uploadURL: 'http://127.0.0.1:8888/api/private/v1/upload',
      // 
      headersObj: {
        Authorization: window.sessionStorage.getItem('token')
      },
      // URL
      previewPath: '',
      //  
      PreviewDialogVisible: false

    }
  },
  created () {
    this.getCateList()
  },
  computed: {
    CateId () {
      if (this.addForm.goods_cat.length === 3) {
        return this.addForm.goods_cat[2]
      }
      return null
    }
  },
  methods: {
    //   
    async getCateList () {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      // console.log(res.data)
      this.CateList = res.data
    },
    //  
    handleChange () {
      if (this.addForm.goods_cat.length !== 3) {
        this.addForm.goods_cat = []
      }
    },
    //
    beforeTabLeave (newActiveName, oldActiveName) {
      if (oldActiveName === '0' && this.addForm.goods_cat.length !== 3) {
        this.$message.error('')
        return false
      }
    },
    //
    async tabClick () {
      // console.log(this.activeIndex)
      if (this.activeIndex === '1') {
        const { data: res } = await this.$http.get(
          `categories/${this.CateId}/attributes`,
          {
            params: {
              sel: 'many'
            }
          }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        this.$message.success('')
        res.data.forEach((item) => {
          item.attr_vals =
            item.attr_vals.length === 0 ? [] : item.attr_vals.split(' ')
        })
        this.manyTabList = res.data
        console.log(this.manyTabList)
      } else if (this.activeIndex === '2') {
        const { data: res } = await this.$http.get(
          `categories/${this.CateId}/attributes`,
          {
            params: {
              sel: 'only'
            }
          }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.onlyTabList = res.data
        console.log(this.onlyTabList)
      }
    },
    // 
    handlePreview (file) {
      this.previewPath = file.response.data.url
      this.PreviewDialogVisible = true
    },
    // 
    handleRemove (file) {
      const filepath = file.response.data.tmp_path
      const i = this.addForm.pics.findIndex(x =>
        x.pic === filepath
      )
      this.addForm.pics.splice(i, 1)
      console.log(this.addForm)
    },
    // 
    handleSuccess (response) {
      // 1.
      const picInfo = { pic: response.data.tmp_path }
      // 2.pushpics
      this.addForm.pics.push(picInfo)
      console.log(this.addForm)
    },
    // 
    add () {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) {
          return this.$message.error('')
        }
        // 
        const form = _.cloneDeep(this.addForm)
        form.goods_cat = form.goods_cat.join(',')
        // 
        this.manyTabList.forEach(item => {
          const newInfo = {
            attr_id: item.attr_id,
            attr_value: item.attr_vals.join(' ')
          }
          this.addForm.attrs.push(newInfo)
        })
        // 
        this.onlyTabList.forEach(item => {
          const newInfo = {
            attr_id: item.attr_id,
            attr_value: item.attr_vals
          }
          this.addForm.attrs.push(newInfo)
        })
        form.attrs = this.addForm.attrs
        console.log(form)

        // 
        const { data: res } = await this.$http.post('goods', form)
        console.log(res)
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.$router.push('/goods')
      })
    }
  }
}
</script>
<style scoped>
.el-checkbox {
  margin: 0 5px 0 0 !important;
}
.btn {
  margin-top:15px
}

</style>
